import React from 'react'
import { Link } from 'react-router-dom'



const HeaderApp = () => (
  <div className="navbar navbar-dark bg-dark shadow-sm">
    <div className="container d-flex justify-content-between">
      <Link to="/"><a className="navbar-brand d-flex align-items-center">
        <strong className="text-light">Bus Ucsal</strong>
      </a></Link>
      <Link to="/"><a className="navbar-brand d-flex align-items-center">
        <strong className="text-light">Informacoes</strong>
      </a>
      </Link>
      <Link to="/localizacao"><a className="navbar-brand d-flex align-items-center">
        <strong className="text-light">Localizacao do bus</strong>
      </a></Link>
    </div>
  </div>
)

export default HeaderApp